












































import { Vue, Component } from 'vue-property-decorator';
import Sider from '../components/Sider.vue';
import crumbs from '../components/crumbs.vue';

const columns = [
  {
    title: '课题',
    dataIndex: 'column1',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '产品代号',
    dataIndex: 'column2',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '批次',
    dataIndex: 'column3',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '实验进去度',
    dataIndex: 'column4',
    scopedSlots: { customRender: 'speed' },
    ellipsis: true,
    width: 190,
    align: 'center',
  },
];
const columns2 = [
  {
    title: '序 号',
    dataIndex: 'column1',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '设备编号',
    dataIndex: 'column2',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '设备名称',
    dataIndex: 'column3',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '现有设备数',
    dataIndex: 'column4',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '操作者',
    dataIndex: 'column5',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '设备缺口/在建数量',
    dataIndex: 'column6',
    ellipsis: false,
    align: 'center',
  },
];
const columns3 = [
  {
    title: '序 号',
    dataIndex: 'column1',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '技改月报',
    dataIndex: 'column2',
    ellipsis: false,
    align: 'center',
  },
  {
    title: '技改建设',
    dataIndex: 'column3',
    ellipsis: false,
    align: 'center',
  },
];
@Component({
  components: { Sider, crumbs },
})
export default class Detail extends Vue {
  current = 1;
  name = '设备台账';
  columns = columns;
  data = [
    { key: '1', column1: '1', column2: '1', column3: '1', column4: 90 },
    { key: '2', column1: '2', column2: '2', column3: '2', column4: 2 },
    { key: '3', column1: '3', column2: '3', column3: '3', column4: 12 },
    { key: '4', column1: '4', column2: '4', column3: '4', column4: 65 },
    { key: '5', column1: '5', column2: '5', column3: '5', column4: 74 },
    { key: '6', column1: '6', column2: '6', column3: '6', column4: 86 },
  ];
  get path() {
    return ['网站首页', '交流分享', this.name];
  }
  onShowSizeChange(page, pageSize) {
    console.log(page, pageSize);
  }
  change(e) {
    switch (e.code) {
      case 'category2':
        this.columns = columns2;
        break;
      case 'category3':
        this.columns = columns3;
        break;
    }

    this.name = e.name;
  }
}
